import {GetAllTeacherList} from '@/api/teacher'
import UniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue'
export default {
	data(){
		return{
			loaded:false,
			teacherlist:[],
			getParameters:{
				pageindex:1,
				pagesize:10,
				key:'',
			},
			pageCount:0,
			moreStatus:'more'
		}
	},
	components:{
		UniLoadMore
	},
	onLoad: function() {
		uni.showLoading({
		    title: '加载中'
		});
		this.loadPage()
	},
	onPullDownRefresh() {
		this.loadPage()
	},
	onReachBottom(e){
		this.getParameters.pageindex++
		this.moreList()
	},
	onShareAppMessage(){
		var that = this
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　　title: '科汛网校7.0的名师分享',
			path: '/pages/teacher/index',
	　　　　imageUrl: '', 
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　}
	　　};
	　　// 返回shareObj
	　　return shareObj;
	},
	methods: {
		open(url){
			uni.navigateTo({
			    url:url
			});
		},
		moreList(){
			if(this.getParameters.pageindex > this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'loading'
				this.loadPage('more')
			}
			
		},
		loadPage(status){
			if(status=='more'){
				GetAllTeacherList(this.getParameters).then(res=>{
					this.moreStatus = 'more'
					if(res.result){
						this.teacherlist = this.teacherlist.concat(res.data)
					}
					this.loaded = true
				})
			}else{
				this.getParameters.pageindex = 1
				GetAllTeacherList(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.pageCount
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
						this.teacherlist = res.data
					}else{
						this.teacherlist = []
						this.pageCount = 0
					}
					this.loaded = true
					uni.stopPullDownRefresh();
					uni.hideLoading();
				})
			}
		}
		
	}
}
